.nav {
  width: 400px;
  display: flex;
  justify-content: space-around;
  text-decoration: none;

  &__link {
    text-decoration: none;
    font-size: 20px;
    font-weight: 500;
    color: #9098a3;

    &--active {
      color: #022147;
      text-decoration: underline;
      font-size: 22px;
      font-weight: 700;
    }
  }
}
